import React from 'react';

import { useFormik } from 'formik';
import toast, { Toaster } from 'react-hot-toast';

import { challange } from '../helper/helper'



export default function Formulaire() {

    const formik = useFormik({
        initialValues : {
          title : 'example12nnn3',
          quetion : 'examplenn123'
        },
        
        onSubmit : async values => {
            values = await Object.assign(values)
            let registerPromise = challange(values)
            toast.promise(registerPromise, {
              loading: 'Creating...',
              success : <b>challange created Successfully...!</b>,
              error : <b>Could not create challange.</b>
            });
        }
      })

  return (
    <div>
      <form class="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
        <div class="mb-4">
          <label class="block text-gray-700 font-bold mb-2" for="challenge-name">
            Challenge Name
          </label>
          <input class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="challenge-name" type="text" placeholder="Challenge Name" />
        </div>
        <div class="mb-4">
          <label class="block text-gray-700 font-bold mb-2" for="points">
            Points
          </label>
          <input class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="points" type="number" placeholder="Points" />
        </div>
        <div class="mb-4">
          <label class="block text-gray-700 font-bold mb-2" for="language">
            Language
          </label>
          <select class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="language">
            <option value="">Select a Language</option>
            <option value="JavaScript">JavaScript</option>
            <option value="Python">Python</option>
            <option value="Java">Java</option>
            <option value="Ruby">Ruby</option>
          </select>
        </div>
        <div class="mb-4">
          <label class="block text-gray-700 font-bold mb-2" for="participation-type">
            Type of Participation
          </label>
          <select class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="participation-type">
            <option value="">Select a Type of Participation</option>
            <option value="solo">Solo</option>
            <option value="collective">Collective</option>
          </select>
        </div>
        <div class="mb-4">
          <label class="block text-gray-700 font-bold mb-2" for="difficulty">
            Difficulty
          </label>
          <select class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="difficulty">
            <option value="">Select a Difficulty Level</option>
            <option value="easy">Easy</option>
            <option value="medium">Medium</option>
            <option value="hard">Hard</option>
          </select>
        </div>
        <div class="mb-4">
          <label class="block text-gray-700 font-bold mb-2" for="date">
            Date
          </label>
          <input class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="date" type="date" placeholder="Date" />
        </div>
        <div class="mb-6">
          <button class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded" type="button">
            Create Challenge
          </button>
</div>
</form>

</div>
);
}